body {
}
.mega-menu-parent {
  .nav {
    position: fixed;
    z-index: 99;
    width: 100%;
    background: #28282b;
  }

  nav .wrapper {
    position: relative;
    /* max-width: 1200px; */
    padding: 0px 25px;
    /* height: 100%; */
    width: 100%;
    line-height: 25px;
    /* margin: auto; */
    /* display: flex; */
    flex-direction: column;
    /* justify-content: space-between; */

  }

  .wrapper .logo {
    color: #ffffff;
    font-size: 35px;
    font-weight: 600;
  }

  .wrapper .logo span {
    color: #38b6ff;
  }

  .wrapper .nav-links {
    display: flex;
    height: 503px;
    flex-direction: column;
    background: white;
    padding-left: 20px;
    box-shadow: 0px 0px 5px 2px #70707024;
    /* padding: 10px 0 1px 30px; */
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .nav-links li {
    list-style: none;
  }

  .nav-links li a {
    color: #000000;
    text-decoration: none;
    font-size: 12px;
    font-weight: 500;

    border-radius: 5px;
    transition: all 0.3s ease;
  }

  .nav-links li a:hover {
    // background: #28282b;
  }

  .nav-links .mobile-item {
    display: none;
  }

  .nav-links .drop-menu {
    position: absolute;
    background: #ffffff;
    width: 180px;
    line-height: 45px;
    top: 85px;
    opacity: 0;
    visibility: hidden;
  }

  .nav-links li:hover .drop-menu,
  .nav-links li:hover .mega-box {
    
    top: 0;
    opacity: 1;
    visibility: visible;
    z-index: 55555;
    left: 216px;
    width: 958px;
    /* height: 100%; */
    background: #ffffff;
    /* max-height: 507px; */
    /* height: 127vh; */
    height: 503px;
  }

  .drop-menu li a {
    width: 100%;
    display: block;
    padding: 0 0 0 15px;
    font-weight: 400;
    border-radius: 0px;
    
  }

  .mega-box {
    position: absolute;
    left: 0;
    width: 100%;
    padding: 0 30px;
    top: 85px;
    opacity: 0;
    visibility: hidden;
  }

  .mega-box .content {
    background: #ffffff;
gap: 30px;
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  .mega-box .content .row {
    width: calc(25% - 30px);
    line-height: 45px;
  }

  .content .row img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .content .row header {
    color: #000000;
    font-size: 12px;
    font-weight: 400;
  
  }

  .content .row .mega-links {

    border-radius: 1px solid rgba(255, 255, 255, 0.09);
  }

  .row .mega-links li {
   
  }

  .row .mega-links li a {
    padding: 0px;
    color: #00000092;
    font-size: 12px;
    display: block;
    font-weight: 400;
    line-height: 25px;
  }

  .row .mega-links li a:hover {
    // color: #000000;
  }

  .wrapper .btn {
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    display: none;
  }

  .wrapper .btn.close-btn {
    position: absolute;
    right: 30px;
    top: 10px;
  }

  /*Media Query*/
  @media screen and (max-width: 1010px) {
    .wrapper .logo {
      font-size: 30px;
    }

    .wrapper .btn {
      display: block;
    }

    .wrapper .nav-links {
      position: fixed;
      height: 100vh;
      width: 100%;
      max-width: 350px;
      top: 0;
      left: -100%;
      background: #28282b;
      display: block;
      padding: 50px 10px;
      line-height: 50px;
      overflow-y: auto;
      box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.18);
      transition: all 0.3s ease;
    }

    /*Custom Scroll bar*/
    ::-webkit-scrollbar {
      width: 10px;
    }

    ::-webkit-scrollbar-track {
      background: #242526;
    }

    ::-webkit-scrollbar-thumb {
      background: #3a3b3c;
    }

    #menu-btn:checked ~ .nav-links {
      left: 0%;
    }

    #menu-btn:checked ~ .btn.menu-btn {
      display: none;
    }

    #close-btn:checked ~ .btn.menu-btn {
      display: block;
    }

    .nav-links li {
      margin: 15px 10px;
    }

    .nav-links li a {
      padding: 0 20px;
      display: block;
      font-size: 20px;
    }

    .nav-links .drop-menu {
      position: static;
      opacity: 1;
      top: 65px;
      visibility: visible;
      padding-left: 20px;
      width: 100%;
      max-height: 0px;
      overflow: hidden;
      box-shadow: none;
      transition: all 0.3s ease;
    }

    #showDrop:checked ~ .drop-menu,
    #showMega:checked ~ .mega-box {
      max-height: 100%;
    }

    .nav-links .desktop-item {
      display: none;
    }

    .nav-links .mobile-item {
      display: block;
      color: #f2f2f2;
      font-size: 20px;
      font-weight: 500;
      padding-left: 20px;
      cursor: pointer;
      border-radius: 5px;
      transition: all 0.3s ease;
    }

    .nav-links .mobile-item:hover {
      // background: #28282b;
    }

    .drop-menu li {
      margin: 0;
    }

    .drop-menu li a {
      border-radius: 5px;
      font-size: 18px;
    }

    .mega-box {
      position: static;
      top: 65px;
      opacity: 1;
      visibility: visible;
      padding: 0 20px;
      max-height: 0px;
      overflow: hidden;
      transition: all 0.3s ease;
    }

    .mega-box .content {
      box-shadow: none;
      flex-direction: column;
      padding: 20px 20px 0 20px;
    }

    .mega-box .content .row {
      width: 100%;
      margin-bottom: 15px;
      border-top: 1px solid rgba(255, 255, 255, 0.08);
    }

    .mega-box .content .row:nth-child(1),
    .mega-box .content .row:nth-child(2) {
      border-top: 0px;
    }

    .content .row .mega-links {
      border-left: 0px;
      padding-left: 15px;
    }

    .row .mega-links li {
      margin: 0;
    }

    .content .row header {
      font-size: 19px;
    }
  }

  nav input {
    display: none;
  }

  .body-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
    padding: 0 30px;
  }

  .body-text {
    font-size: 45px;
    font-weight: 600;
  }
}
